.root {
  display: flex;
  height: 100%;
  :global {
    .left {
      height: 100%;
      flex: 1;
    }
    .right {
      flex: 4;
      height: 100%;
      overflow-x: scroll;
      overflow: hidden;
      .circuit {
        height: 50%;
        overflow: hidden;
        overflow-y: scroll;
        border: 1px solid black;
      }
      .down-right {
        width: 100%;
        height: 50%;
        border: 1px solid black;
        .evolution {
          display: inline-block;
          width: 50%;
          height: 100%;
          border-left: 1px solid #000;
          border-right: 1px solid #000;
          .toplogicalFather {
            overflow: hidden;
            overflow-y: 'scroll';
            overflow-x: 'scroll';
            height: '100%';
            width: '100%';
            .Topological {
              border: 1px solid rgb(198, 193, 183);
              margin: 10px;
              position: relative;
              .icon {
                float: right;
                width: 25px;
              }
              .overperation {
                position: absolute;
                right: -8px;
                width: 25px;
                border: 1px solid rgb(198, 193, 183);
              }
            }
          }
          .title {
            margin-top: 10px;
            display: flex;
            width: 100%;
            justify-content: space-between;
          }
        }
        .down_right_c{
          display: inline-block;
          width: 50%;
          height: 500px;
          vertical-align: top;
          .variable ,.system {
            vertical-align: top;
            overflow: 'hidden';
            overflow-y: 'scroll';  
            width: 100%;
            height: 260;
            .title {
              font-size: 16px;
              margin-top: 8px;
             .icon{margin-left: 10px;}
            }
          }
          .system{
            height: 500px;
          }
        }
       
      }
    }
  }
}
